<template>
  <div>
    <v-navigation-drawer v-model="drawer"
                         app temporary
    >
      <v-list>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-subtitle>EasyQuiz</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-divider/>

      <v-list dense>
        <v-list-item
            v-for="([icon, text, link], i) in items"
            :key="i"
            link
            @click="$vuetify.goTo(link)"
        >
          <v-list-item-icon class="justify-center">
            <v-icon>{{ icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title class="subtitile-1">{{
                text
              }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item :to="{name: 'Login'}">
          <v-list-item-icon class="justify-center">
            <v-icon>mdi-login</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title class="subtitile-1">Đăng nhập</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
        app
        :color="color"
        :flat="flat"
        dark
        class="px-5"
        :class="{ expand: flat }"
    >
      <v-row align="center" justify="center">
        <v-col cols="10">
          <v-app-bar-nav-icon
              @click.stop="drawer = !drawer"
              v-if="isXs" class="float-end"
          />
          <div v-else class="float-end">
            <template v-for="([, text, link], i) in items">
              <v-btn rounded :to="link" color="secondary" :key="i">
                <span>{{ text }}</span>
              </v-btn>
              &nbsp;
            </template>
            <template v-if="authenticated">
              <v-btn rounded :to="{name: 'Dashboard'}" color="secondary">
                <span>Bảng điều khiển ({{ user.name }})</span>
              </v-btn>
              &nbsp;
              <v-btn rounded :to="{name: 'Admin Dashboard'}" v-if="isAdmin" color="secondary">
                <span>Quản trị Website</span>
              </v-btn>
              &nbsp;
              <v-btn rounded @click="logout" color="secondary">
                <span>Đăng xuất</span>
              </v-btn>
              &nbsp;
            </template>
            <template v-else>
              <v-btn rounded :to="{name: 'Login'}" color="secondary">
                <span>Đăng nhập</span>
              </v-btn>
              &nbsp;
            </template>
          </div>
        </v-col>
      </v-row>
    </v-app-bar>
  </div>
</template>

<style scoped>
.v-toolbar {
  transition: 0.6s;
}

.expand {
  height: 80px !important;
  padding-top: 10px;
}
</style>

<script>
import {LOGOUT} from "@/store/actions/type";
import {ADMIN} from "@/enum/role";

export default {
  data: () => ({
    drawer: null,
    isXs: false,
    items: [
      ["mdi-home-outline", "Trang chủ", "/#"],
      ["mdi-information-outline", "Trắc nghiệm", "/quiz"],
      ["mdi-information-outline", "Tự luận", "/essay-exam"],
    ],
  }),
  props: {
    color: String,
    flat: Boolean,
  },
  methods: {
    onResize() {
      this.isXs = window.innerWidth < 850;
    },
    logout() {
      this.$store.dispatch(LOGOUT).then(() => {
        this.authenticated = false;
        this.$router.push('/');
      });
    }
  },

  watch: {
    isXs(value) {
      if (!value) {
        if (this.drawer) {
          this.drawer = false;
        }
      }
    },
  },
  computed: {
    user() {
      return this.$store.getters.currentUser
    },
    authenticated() {
      return this.$store.state.auth.isAuthenticated
    },
    isAdmin(){
      return this.$store.getters.currentUser.role === ADMIN
    }
  },
  mounted() {
    this.onResize();
    window.addEventListener("resize", this.onResize, {passive: true});
  },
};
</script>
