<template>
  <v-app>
    <navigation :color="color" :flat="flat"/>
    <v-main class="pt-0">
      <section id="hero">
        <div class="header-img">
          <v-row align="center" justify="center">
            <v-col cols="10">
              <v-row align="center" justify="center">
                <v-col cols="12" md="6" xl="8">
                  <h1 class="display-2 font-weight-bold mb-4">EasyQuiz</h1>
                  <h1 class="font-weight-light">
                    Tìm kiếm đề thi và bắt đầu thử sức<br>
                  </h1>
                  <v-btn rounded outlined
                         @click="$vuetify.goTo('#quizzes')"
                         class="mt-5"
                  >
                    Đề thi
                    <v-icon class="ml-2">mdi-arrow-down</v-icon>
                  </v-btn>
                </v-col>
                <v-col cols="12" md="6" xl="4" class="hidden-sm-and-down"></v-col>
              </v-row>
            </v-col>
          </v-row>
        </div>
        <router-view></router-view>
      </section>
    </v-main>
    <v-scale-transition>
      <v-btn fab v-show="fab" v-scroll="onScroll" dark fixed bottom right color="secondary" @click="toTop">
        <v-icon>mdi-arrow-up</v-icon>
      </v-btn>
    </v-scale-transition>
    <foote/>
  </v-app>
</template>

<style lang="scss" scoped>
.v-main {
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
.header-img{
  display: flex;
  position: relative;
  background-image: url('~@/assets/img/background.png');
  background-repeat: no-repeat;
  background-size: 100%;
  height: 300px;

}
</style>

<script>
import navigation from "@/components/homepage/Navigation";
import foote from "@/components/homepage/Footer";

export default {
  name: "guest-wrapper",

  components: {
    navigation,
    foote,
  },

  data: () => ({
    fab: null,
    color: "",
    flat: null,
    quizView: false
  }),

  created() {
    const top = window.pageYOffset || 0;
    if (top <= 60) {
      this.color = "transparent";
      this.flat = true;
    }
  },

  watch: {
    fab(value) {
      if (value) {
        this.color = "secondary";
        this.flat = false;
      } else {
        this.color = "transparent";
        this.flat = true;
      }
    },
  },

  methods: {
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 60;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
  },
};
</script>
