import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store'

Vue.use(VueRouter);

const auth = (to, from, next) => {
    if (store.getters.isAuthenticated) {-
        next();
        return
    }
    next('/login?redirect=' + to.path);
}

import Homepage from "@/views/guest/Homepage.vue";
import Login from "@/views/guest/Login.vue";
import Register from "@/views/guest/Register.vue";
import AuthWrapper from "@/views/auth/AuthWrapper.vue";
import GuestWrapper from "@/views/guest/GuestWrapper.vue";
import QuizView from "@/views/guest/QuizView.vue";
import AdminWrapper from "@/views/auth/AdminWrapper.vue";

const routes = [
    {
        path: '/dashboard',
        component: AuthWrapper,
        beforeEnter: auth,
        children: [
            {
                name: 'Dashboard',
                path: '/dashboard',
                component: () => import('@/views/dashboard/pages/Dashboard.vue'),
            },
            {
                name: 'AddQuiz',
                path: '/quizzes/add',
                component: () => import('@/views/dashboard/pages/Quiz/ManageQuiz'),
            },
            {
                name: 'EditQuiz',
                path: '/quizzes/:id/edit',
                component: () => import('@/views/dashboard/pages/Quiz/ManageQuiz'),
            },
            {
                name: 'Quiz',
                path: '/quizzes/:id',
                component: () => import('@/views/dashboard/pages/Quiz/Quiz'),
            },
            {
                name: 'RunningQuiz',
                path: '/quizzes/:id/running',
                component: () => import('@/views/dashboard/pages/Quiz/Running'),
            },
            {
                name: 'TestResult',
                path: '/attempts/:id',
                component: () => import('@/views/dashboard/pages/Quiz/TestResult'),
            },
            {
                name: 'AttachMCQ',
                path: '/quizzes/:id/attach',
                component: () => import('@/views/dashboard/pages/Quiz/AttachMCQ'),
            },
            {
                name: 'Quiz\'s',
                path: '/quizzes',
                component: () => import('@/views/dashboard/pages/Quiz/Quizzes'),
            },
            {
                name: 'AddMCQ',
                path: '/mcq/add',
                component: () => import('@/views/dashboard/pages/MCQ/ManageMCQ'),
            },
            {
                name: 'EditMCQ',
                path: '/mcq/:id/edit',
                component: () => import('@/views/dashboard/pages/MCQ/ManageMCQ'),
            },
            {
                name: 'MCQ',
                path: '/mcq/:id',
                component: () => import('@/views/dashboard/pages/MCQ/MCQ'),
            },
            {
                name: 'MCQ\'s',
                path: '/mcq',
                component: () => import('@/views/dashboard/pages/MCQ/MCQs'),
            },
            {
                name: 'TestAttempts',
                path: '/attempts',
                component: () => import('@/views/dashboard/pages/Quiz/Attempts'),
            },

        ]
    },
    {
        path: '/admin/dashboard',
        component: AdminWrapper,
        beforeEnter: auth,
        children: [
            {
                name: 'Admin Dashboard',
                path: '/admin/dashboard',
                component: () => import('@/views/dashboard/admin/Dashboard.vue'),
            },
            {
                name: 'Admin AddQuiz',
                path: '/admin/quizzes/add',
                component: () => import('@/views/dashboard/admin/Quiz/ManageQuiz'),
            },
            {
                name: 'Admin EditQuiz',
                path: '/admin/quizzes/:id/edit',
                component: () => import('@/views/dashboard/admin/Quiz/ManageQuiz'),
            },
            {
                name: 'Admin TestResult',
                path: '/admin/attempts/:id',
                component: () => import('@/views/dashboard/admin/Quiz/TestResult'),
            },
            {
                name: 'Admin Quiz',
                path: '/admin/quizzes/:id',
                component: () => import('@/views/dashboard/admin/Quiz/Quiz'),
            },
            {
                name: 'Admin AttachMCQ',
                path: '/admin/quizzes/:id/attach',
                component: () => import('@/views/dashboard/admin/Quiz/AttachMCQ'),
            },
            {
                name: 'Admin Quiz\'s',
                path: '/admin/quizzes',
                component: () => import('@/views/dashboard/admin/Quiz/Quizzes'),
            },
            {
                name: 'Admin AddMCQ',
                path: '/admin/mcq/add',
                component: () => import('@/views/dashboard/admin/MCQ/ManageMCQ'),
            },
            {
                name: 'Admin EditMCQ',
                path: '/admin/mcq/:id/edit',
                component: () => import('@/views/dashboard/admin/MCQ/ManageMCQ'),
            },
            {
                name: 'Admin MCQ',
                path: '/admin/mcq/:id',
                component: () => import('@/views/dashboard/admin/MCQ/MCQ'),
            },
            {
                name: 'Admin MCQ\'s',
                path: '/admin/mcq',
                component: () => import('@/views/dashboard/admin/MCQ/MCQs'),
            },
            {
                name: 'Admin TestAttempts',
                path: '/admin/attempts',
                component: () => import('@/views/dashboard/admin/Quiz/Attempts'),
            },

        ]
    },
    {
        path: "/",
        component: GuestWrapper,
        children: [
            {
                path: "/",
                name: "Homepage",
                component: Homepage,
            },
            {
                path: "/quiz-view/:id",
                name: "QuizView",
                component: QuizView,
            },
            {
                path: "/login",
                name: "Login",
                component: Login,
            },
            {
                path: "/register",
                name: "Register",
                component: Register,
            }
        ]
    },
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
});

export default router;
