<template>
  <v-container fluid id="quizview" class="mt-2">
    <v-row align="center" justify="center" v-if="quiz">
      <v-col cols="10">
        <v-breadcrumbs :items="breadcrumbs" divider="-"></v-breadcrumbs>
        <v-card class="mx-auto" max-width="500">
          <v-card-text class="pt-6">
            <div class="text-h3 pb-3">{{ quiz.title }}</div>
            <p class="text-h4 text--primary">
              {{ quiz.description }}
            </p>
            <p>Author: <a :href="`mailto:${quiz.author_email}`">{{ quiz.author }}</a></p>
            <div class="text--primary">
              Time Limit: {{
                moment.utc(moment.duration(quiz.time_limit, 'seconds').as('milliseconds')).format('HH:mm:ss')
              }}
            </div>
            <p>Highest Score: {{ quiz.high_score }}</p>
          </v-card-text>
          <v-card-actions class="pb-5">
            <v-spacer></v-spacer>
            <v-btn color="secondary" :to="{name: 'RunningQuiz', params: {id: quiz.id}}">
              Bắt đầu
            </v-btn>
          </v-card-actions>
        </v-card>
        <br/><br/>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ApiService from "@/common/api.service";

export default {
  name: 'QuizView',
  data() {
    return {
      quiz: null,
      breadcrumbs: [
        {
          text: 'Trang chủ',
          disabled: false,
          href: '/',
        },
        {
          text: 'Đề thi trắc nghiệm',
          disabled: true,
          href: '/quiz-view/' + this.$route.params.id,
        },
      ],
    }
  },
  mounted() {
    let id = this.$route.params.id;
    if (id) {
      this.getQuiz(id);
    } else {
      this.$toastr.e("Đề thi không tồn tại!")
    }
  },
  methods: {
    getQuiz(id) {
      this.loading = true;
      ApiService.get(`/pub/quiz/${id}`).then(res => {
        this.quiz = res.data.data;
        this.loading = false;
      }).catch(err => {
        this.$toastr.e(err)
      })
    },
  }
}
</script>
